/** This file centralize the styling of client website */

/* all pages */
.scholly-header {
  top: 0;
}

/* top padding for each page, to avoid overlapping with header */
.pages-top-padding {
  padding-top: 50px;
}

/* landing pages */
.landing-carousel-img {
  height: 300px;
  width: 100%;
  overflow: hidden;
  object-fit: cover;
}

.left {
  float: left;
  padding: 0 20px 20px 0;
  width: 50%;
  height: 30%;
}

.landing-center-title {
  text-align: center;
  padding-top: 25px;
  font-weight: bolder;
  padding-left: 40px;
  padding-right: 40px;
  font-size: xx-large;
}

.landing-center-content {
  text-align: center;
  padding-top: 20px;
  padding-left: 40px;
  padding-right: 40px;
  font-size: medium;
}

/* publication page */
.publication-pg-title {
  text-align: center;
  padding-top: 8px;
  font-weight: 460;
  padding-left: 40px;
  padding-right: 40px;
  font-size: xx-large;
}

.publication-title {
  font-size: large;
  text-align: center;
  font-weight: 600;
}

.publication-title-column {
  background-color: white;
  border: transparent;
}

.publication-body-column {
  border: transparent;
}

.publication-card {
  border: transparent;
  padding-bottom: 15px;
  text-align: center;
}

.pub-category-above-title {
  font-style: italic;
  font-size: medium;
  text-align: center;
  font-weight: 450;
}

.pub-year-below-title {
  font-size: smaller;
  text-align: center;
}

.pub-body-subheader {
  text-align: center;
  font-style: italic;
  font-size: medium;
  font-weight: 450;
}

.pub-body-content {
  text-align: center;
  font-size: medium;
  padding-bottom: 15px;
}

.pub-body-paragraph {
  text-indent: 1%;
  text-align: justify;
  text-align-last: center;
}

.pub-twitter-right {
  float: right;
  width: 20%;
  height: inherit;
  max-width: 333px;
  padding: 16px;
}

@media screen and (max-width: 1024px) {
  .pub-twitter-right {
    display: none !important;
  }
}

.twitter-feed {
  border: 1px solid #ccc !important;
  border-radius: 16px;
  margin-top: 60px;
  position: fixed;
}

.pub-left-main {
  width: 80%;
  float: left;
}


.twitter-feed {
  border: 1px solid #ccc !important;
  border-radius: 16px;
  margin-top: 60px;
  position: fixed;
}

.pub-left-main {
  width: 80%;
  float: left;
}

/* team page */
.team-pg-title {
  text-align: center;
  padding-top: 8px;
  font-weight: bolder;
  padding-left: 40px;
  padding-right: 40px;
  font-size: xx-large;
}

.teamMemberBody {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 24px;
}

.team-member-name {
  font-weight: bold;
  color: rgb(49, 49, 49);
  font-size: 18px;
}

.team-member-position {
  font-weight: bold;
  font-size: 15px;
}

.team-member-summary {
  font-size: 15px;
}

.team-card-container {
  padding-left: 50px;
  padding-right: 50px;
}

.team-card {
  border: transparent;
  min-width: 210px;
  max-width: 210px;
}

.card-deck {
  align-content: center;
  justify-content: center;
  border-top-style: solid;
  border-top-color:grey;
  border-width: 1px;
  padding-top: 20px;
}

.button-pub{
  margin: 10px 0 5px 0;
}

/* achievement page */

.achievementBody {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#achievementImage {
  padding-top: 20px;
}

.award-name {
  font-weight: bold;
  color: rgb(49, 49, 49);
  font-size: 15px;
}

.award-year {
  font-family: "Lato", sans-serif;
  font-size: 15px;
}

.award-summary {
  font-size: 15px;
}

.fontx {
  font-family: "Verdana", sans-serif;
}

